import EvanCheckbox from '@/components/evan-checkbox/evan-checkbox.vue'
import EvanCheckboxGroup from '@/components/evan-checkbox/evan-checkbox-group.vue'
import UniIcons from '@/components/uni-icons/uni-icons.vue'
import {GetAddlist,RemoveAdd} from "../../common/api.js"
export default {
	data() {
		return {
			list:[]
		}
	},
	components: {
		EvanCheckbox,
		EvanCheckboxGroup,
		UniIcons
	},
	onLoad(option){
		
		this.pageInit();
	},
	methods: {
		addAddress(){
			uni.navigateTo({
			    url: '../addAddress/addAddress?type=a'
			});
		},
		setDefault(param){
			if(param.default){
				param.default = true;
			}
		},
		pageInit(){
			GetAddlist().then(res=>{
				if(res.status){
					let d = [...res.data]
					for(let i = 0,len=d.length;i<len;i++){
						if(d[i].default===1){
							d[i].default = true;
						}else{
							d[i].default = false;
						}
					}
					this.list = [...d];
				}
			})
		},
		toEdit(id){
			uni.navigateTo({
			    url: '../editAddress/editAddress?aid='+id+'&type=a'
			});
		},
		remove(id){
			let _this = this;
			uni.showModal({
			    title: '提示',
			    content: '确定要删除吗？',
			    success: function (res) {
			        if (res.confirm) {
						let d = {id:id}
			            RemoveAdd(d).then(res=>{
							if(res.status){
								_this.pageInit();
							}
						})
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		}
		
	}
}
